		}
	}

/*顺时针旋转*/
	@-webkit-keyframes rotateSequential {
		0% {
			-webkit-transform: rotate(0deg);
		}
		49% {
			-webkit-transform: rotate(180deg);
		}
		98% {
			-webkit-transform: rotate(380deg);
		}
		100% {
