/*上浮淡出*/
	@-webkit-keyframes floatTopFade {
		0% {
			opacity: 0;
			-webkit-transform: translateY(90%) scale(0.3);
		}
		10% {
			opacity: 1;
			-webkit-transform: translateY(83%) scale(0.7);
		}
		90% {
			opacity: 1;
			-webkit-transform: translateY(-50%) scale(1);
		}
		98% {
			opacity: 0;
			-webkit-transform: translateY(-65%) scale(1);
		}
		100% {
			opacity: 0;
			-webkit-transform: translateY(90%) scale(0.3);
		}
	}
